import { BuildContext, Button, Component, Container, getServerActionsClient, Heading, Row, StatefulComponent, TextInput } from '@equantic/runtime';

export class TodoList extends StatefulComponent {
  createState(): TodoListState {
    return new TodoListState(this);
  }

  async getTodos(): Promise<Todo[]> {
    return await getServerActionsClient().invoke('TodoList/GetTodos', []);
  }

  async addTodo(title: string): Promise<Todo> {
    return await getServerActionsClient().invoke('TodoList/AddTodo', [title]);
  }

  async toggleTodo(id: string): Promise<void> {
    return await getServerActionsClient().invoke('TodoList/ToggleTodo', [id]);
  }

  async deleteTodo(id: string): Promise<void> {
    return await getServerActionsClient().invoke('TodoList/DeleteTodo', [id]);
  }
}

class TodoListState extends ComponentState {
  private _component: TodoList;
  private _needsRender: boolean = false;

  private _todos: Todo[] = [];
  private _newTodoTitle: string = "";

  constructor(component: TodoList) {
    super();
    this._component = component;
  }

  setState(fn: () => void): void {
    fn();
    this._needsRender = true;
    this._component._scheduleRender();
  }

  loadTodos(): void 

  handleAdd(): void 

  handleToggle(id: string): void 

  handleDelete(id: string): void 

  build(context: BuildContext): Component {
return new Container({
      className: 'todo-container',
children: [
new Heading({
          content: 'eQuantic Todo List',
})        ,
new Row({
          gap: '10px',
children: [
new TextInput({
              value: this._newTodoTitle,
              placeholder: 'What needs to be done?',
              onChange: (val) => this.setState(() => this._newTodoTitle = val),
})            ,
new Button({
              text: 'Add',
              className: 'btn btn-primary',
              onClick: () => { HandleAdd(); },
})            ,
]
})        ,
]
})    ;
  }
}
